
.p-card {
    display: inline-block;
    position: relative;
    background: $white;
    color: $gray;
    // box-shadow: 0px 3px 30px $black;
    box-shadow: 0px 3px 30px rgba(0,0,0,0.3);
    transition: .3s ease;
    width: 100%;
    
    @include mq(tab) {
        // max-width: calc(33% - rem(25) * 2 / 3);
    }
}

.p-card:hover {
    @include mq (tab) {
        background: $mid-gray;
        color: $white;
        transition: .3s ease;
    }
}

.p-card__img {
    padding-top: 66.567164%; // 223 / 335
    position: relative;

    img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 100%;
        object-fit: cover; //トリミング
    }
}


