.p-card__content {
    letter-spacing: rem(1);
    padding: rem(4) rem(23) rem(16);

    @include mq (tab) {
        padding: rem(4) rem(23) rem(17);
    }
}

.p-card__title {
    text-align: center;
    font-size: rem(18);
    font-weight: $medium;
    line-height: rem(35);
}

.p-card__text {
    font-size: rem(14);
    font-weight: $font-weight-base;
    line-height: rem(26);
    margin-top: rem(2);

    @include mq (tab) {
        margin-top: rem(6);
    }
}

.p-card__info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    line-height: 1;
    margin-top: auto;

    font-size: rem(14);
    font-weight: $font-weight-base;
    line-height: rem(26);
    margin-top: rem(7);

    @include mq (tab) {
        margin-top: rem(8);
    }

}

.p-card__category {
    background: $orange;
    border-radius: 2px;
    // background: $yellow;
    color: $gray;
    padding: 0 rem(3);
    letter-spacing: rem(1);
    @include mq (tab) {
        padding: 0 rem(6);
    }
}

.p-card__date {
    letter-spacing: rem(1);
}